body {
  line-height: 2px;
  user-select: none;
}

.brandname_rainbow_animation {
  animation: rainbow;
  animation-duration: 5s;
  animation-iteration-count: infinite;
}

.reputation_bad {
  animation: r_bad;
  animation-duration: 5s;
  animation-iteration-count: infinite;
}

@keyframes rainbow {
  0% {
    color: rgba(255, 67, 67, 2);
    text-shadow: 0px 0px 50px rgba(255, 67, 67, 1);
  }
  25% {
    color: rgba(255, 252, 67, 2);
    text-shadow: 0px 0px 50px rgba(255, 252, 67, 1);
  }
  50% {
    color: rgba(67, 255, 90, 2);
    text-shadow: 0px 0px 50px rgba(67, 255, 90, 1);
  }
  75% {
    color: rgba(67, 85, 255, 2);
    text-shadow: 0px 0px 50px rgba(67, 85, 255, 1);
  }
  100% {
    color: rgba(255, 67, 67, 2);
    text-shadow: 0px 0px 50px rgba(255, 67, 67, 1);
  }
}
@keyframes r_bad {
  0% {
    text-shadow: 0px 0px 25px rgba(255, 67, 67, 1);
  }
  25% {
    text-shadow: 0px 0px 25px rgba(0, 0, 0, 0);
  }
  50% {
    text-shadow: 0px 0px 25px rgba(255, 67, 67, 1);
  }
  75% {
    text-shadow: 0px 0px 25px rgba(0, 0, 0, 0);
  }
  100% {
    text-shadow: 0px 0px 25px rgba(255, 67, 67, 1);
  }
}
